.catalog {
  overflow: hidden;

  &__title {
    display: none;
  }

  &__tabs-wrap {
    padding-top: 25px;
    padding-left: 30px;
    padding-bottom: 5px;
    background-color: $gray-lighter;

    @media (min-width: $screen-md) {
      padding-left: 90px;
    }

    @media (min-width: $screen-lg) {
      padding-left: 0;
    }
  }

  &__tabs {
    display: flex;

    @media (min-width: $screen-lg) {
      margin: 0 auto;
      padding-left: 100px;
      width: 1160px;
    }
  }

  &__tab-item {
    margin-right: 30px;

    @media (min-width: $screen-lg) {
      margin-right: 48px;
    }
  }

  &__tab-link {

    &.active {
      padding-bottom: 15px;
      color: $second-color;
      border-bottom: 2px solid $third-color-dark;
    }

    &:hover {
      color: $second-color;
    }
  }

  &__list-wrap {
    padding: 40px 30px 0;
    margin: 0 auto;
    width: 100%;

    @media (min-width: $screen-md) {
      padding: 50px 75px 0;
      width: 760px;
    }

    @media (min-width: $screen-lg) {
      padding: 55px 0 0;
      width: 1160px;
    }
  }

  &__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    @media (min-width: $screen-md) {
      margin: 0 -45px;
      justify-content: flex-start;
    }

    @media (min-width: $screen-lg) {
      padding: 0;
      margin: 0 -20px;
    }
  }

  &__col {
    padding-bottom: 40px;

    @media (min-width: $screen-md) {
      padding: 0 45px 40px;
      width: 50%;
    }

    @media (min-width: $screen-lg) {
      padding: 0 20px 30px;
      width: 25%;
    }
  }
}
